import React from "react";
// Framer Motion import for future animation
// import { motion } from "framer-motion";

const heroBg = "https://images.unsplash.com/photo-1517841905240-472988babdf9?auto=format&fit=crop&w=1200&q=80"; // Example luxury jewelry bg

const featuredImages = [
  "https://images.unsplash.com/photo-1517841905240-472988babdf9?auto=format&fit=crop&w=400&q=80", // Wedding Rings
  "https://images.unsplash.com/photo-1464983953574-0892a716854b?auto=format&fit=crop&w=400&q=80", // Engagement Rings
  "https://images.unsplash.com/photo-1506744038136-46273834b3fb?auto=format&fit=crop&w=400&q=80", // Custom Designs
];
const bestsellersImages = [
  process.env.PUBLIC_URL + '/ringstestimages/three stone gold round.jpg',
  process.env.PUBLIC_URL + '/ringstestimages/three stone platinum round.jpg',
  process.env.PUBLIC_URL + '/ringstestimages/vintage gold ring diamond round.jpg',
  process.env.PUBLIC_URL + '/ringstestimages/halo seting gold ring.jpg',
];

const Home = () => (
  <div className="bg-white min-h-screen font-sans">
    {/* Hero Section */}
    <section className="w-full flex flex-col items-center justify-center text-center py-24 px-4 relative overflow-hidden" style={{ background: 'linear-gradient(135deg, #111 60%, #bfa14a 100%)', minHeight: '60vh' }}>
      <div className="absolute inset-0 opacity-60" style={{ background: 'url("/ringstestimages/diamond seting ring platinum  diamond rectangular.jpeg") center/cover no-repeat' }}></div>
      <div className="absolute inset-0 bg-gradient-to-br from-black/70 via-black/30 to-gold/60" style={{ pointerEvents: 'none' }}></div>
      <div className="relative z-10 animate-fadein">
        <h1 className="text-5xl md:text-7xl font-serif font-extrabold mb-4 text-gold drop-shadow-lg" style={{ fontFamily: 'Playfair Display, serif', letterSpacing: '-1px' }}>
          Shemesh Gold
        </h1>
        <p className="text-2xl md:text-3xl mb-8 text-white font-light max-w-2xl mx-auto" style={{ textShadow: '0 2px 8px #0008' }}>
          Exquisite Rings for Life's Most Precious Moments
        </p>
        <a href="/shop" className="inline-block px-12 py-4 bg-gold text-black font-bold rounded-full shadow-lg hover:bg-black hover:text-gold transition-all duration-300 text-xl tracking-wide border-2 border-gold hover:scale-105 focus:outline-none focus:ring-4 focus:ring-gold/40">
          Shop Now
        </a>
      </div>
      <style>{`
        .text-gold { color: #bfa14a; }
        .bg-gold { background-color: #bfa14a; }
        @keyframes fadein { from { opacity: 0; transform: translateY(40px); } to { opacity: 1; transform: none; } }
        .animate-fadein { animation: fadein 1.2s cubic-bezier(.4,0,.2,1) both; }
      `}</style>
    </section>
    {/* Divider */}
    <div className="w-full border-t border-gray-200 my-12" />
    {/* Featured Collections */}
    <section className="max-w-6xl mx-auto py-12 px-4">
      <h2 className="text-3xl font-serif font-bold text-black mb-10 text-center" style={{ fontFamily: 'Playfair Display, serif' }}>
        Featured Collections
      </h2>
      <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
        <div className="bg-white rounded-lg shadow p-8 border border-gray-200 text-center flex flex-col items-center">
          <img src={process.env.PUBLIC_URL + '/ringstestimages/three stone gold square.jpg'} alt="Wedding Rings" className="w-32 h-32 object-cover rounded-full mx-auto" />
          <h3 className="font-serif text-xl font-bold mb-2" style={{ fontFamily: 'Playfair Display, serif' }}>Wedding Rings</h3>
          <p className="text-gray-700">Timeless bands for your forever.</p>
        </div>
        <div className="bg-white rounded-lg shadow p-8 border border-gray-200 text-center flex flex-col items-center">
          <img src={process.env.PUBLIC_URL + '/ringstestimages/three stone platinum square.jpg'} alt="Engagement Rings" className="w-32 h-32 object-cover rounded-full mx-auto" />
          <h3 className="font-serif text-xl font-bold mb-2" style={{ fontFamily: 'Playfair Display, serif' }}>Engagement Rings</h3>
          <p className="text-gray-700">Celebrate your unique love story.</p>
        </div>
        <div className="bg-white rounded-lg shadow p-8 border border-gray-200 text-center flex flex-col items-center">
          <img src={process.env.PUBLIC_URL + '/ringstestimages/vintage rose square.jpg'} alt="Custom Designs" className="w-32 h-32 object-cover rounded-full mx-auto" />
          <h3 className="font-serif text-xl font-bold mb-2" style={{ fontFamily: 'Playfair Display, serif' }}>Custom Designs</h3>
          <p className="text-gray-700">Design a ring as unique as you.</p>
        </div>
      </div>
    </section>
    <div className="w-full border-t border-gray-200 my-12" />
    {/* Promo Banner */}
    <section className="w-full text-center py-6">
      <span className="font-bold text-lg text-gold">Summer Sale: 10% Off All Custom Rings!</span>
    </section>
    <div className="w-full border-t border-gray-200 my-12" />
    {/* Testimonials */}
    <section className="max-w-4xl mx-auto py-12 px-4">
      <h2 className="text-3xl font-serif font-bold text-black mb-10 text-center" style={{ fontFamily: 'Playfair Display, serif' }}>
        What Our Customers Say
      </h2>
      <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
        <div className="bg-white rounded-lg shadow p-8 border border-gray-200 text-left">
          <p className="italic text-lg mb-4">“Absolutely stunning craftsmanship. My fiancée loved her ring!”</p>
          <span className="block text-gold font-semibold">– Daniel S.</span>
        </div>
        <div className="bg-white rounded-lg shadow p-8 border border-gray-200 text-left">
          <p className="italic text-lg mb-4">“The custom design process was so easy and fun. Highly recommend!”</p>
          <span className="block text-gold font-semibold">– Sarah L.</span>
        </div>
      </div>
    </section>
    <div className="w-full border-t border-gray-200 my-12" />
    {/* Bestsellers Grid */}
    <section className="max-w-6xl mx-auto py-12 px-4">
      <h2 className="text-3xl font-serif font-bold text-black mb-10 text-center" style={{ fontFamily: 'Playfair Display, serif' }}>
        Bestsellers
      </h2>
      <div className="grid grid-cols-2 md:grid-cols-4 gap-8">
        {bestsellersImages.map((img, i) => (
          <div key={i} className="bg-white rounded-lg shadow p-6 border border-gray-200 flex flex-col items-center justify-center text-black">
            <img src={img} alt={`Bestseller Ring ${i+1}`} className="w-24 h-24 object-cover rounded-full mb-4 border border-gray-200 shadow-inner" />
            <span className="font-serif text-lg font-bold mb-2" style={{ fontFamily: 'Playfair Display, serif' }}>{`Ring ${i+1}`}</span>
          </div>
        ))}
      </div>
    </section>
  </div>
);

export default Home; 